<template>
  <div id="app">
    <router-view class="content"/>
    <div class="player">
      <aplayer
        fixed
        :mini="false"
        :listFolded="true"
        v-if="songList[0]"
        ref="aplayer" 
        :audio="songList"
        :lrcType="3"
        theme="#fe9a3b"
        @play="playSong()"
        @pause="pauseSong()"
      />
      <a href="#" class="info-btn" @click.prevent="songURL()"><i class="fas fa-info-circle"></i></a>
    </div>
    <Menu></Menu>
  </div>
</template>

<script>
import firebase from 'firebase';
import $ from 'jquery';
import Menu from './components/Menu.vue'

export default {
  name: 'App',
  data() {
    return {
      songList: [],
      user: null,
    }
  },
  components: {
    Menu
  },
  methods: {
    loginOrNot() {
      const vm = this;
      firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
          vm.user = user;
        } else {
          vm.user = 'not-log-in';
        }
      });
    },
    songURL() {
      // 歌曲詳細頁面
      const vm = this;
      const obj = Object.assign({}, vm.$refs.aplayer.currentMusic)
      vm.$router.push(`/song/${obj.id}`);
    },
    playSong() {
      const vm = this;
      const mId = vm.$refs.aplayer.currentMusic.id;
      const now = new Date().getTime();
      const oneMin = 60*1000;
      const storageStr = localStorage.getItem('viewCount');
      let thisView = {};
      vm.$bus.$emit('current:id', mId);
      if(storageStr) {
        let storageArr = JSON.parse(storageStr);
        if (storageArr[mId]) {
          const mm = new Date(storageArr[mId]);
          const count = mm.setMinutes(mm.getMinutes() + 3);
          if(count < now) {
            // 如果已經超過三分鐘
            storageArr[mId] = now;
            vm.echionCount(storageArr);
          }
        } else {
          // 沒有播過這首
          storageArr[mId] = now;
          vm.echionCount(storageArr);
        }
      } else {
        // 都沒播過迷聲的歌
        thisView[mId] = now;
        vm.echionCount(thisView);
      }
    },
    pauseSong() {
      const vm = this;
      const mId = vm.$refs.aplayer.currentMusic.id;
      vm.$bus.$emit('current:pause', mId);
    },
    echionCount(val) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/addViews`;
      const mId = vm.$refs.aplayer.currentMusic.id;
      const reqData = vm.$refs.aplayer.currentMusic;
      
      localStorage.setItem('viewCount', JSON.stringify(val))
      vm.$http.post(url, {"reqData": reqData}).then((response) => {
        const reqObj = {
          id: mId,
          num: response.data.result + 1
        }
        if(response.data.success){
          vm.$bus.$emit('add:view', reqObj);
        }
      })
    },
    getMusicData(data) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getPostData`;
      const reqURL = `/music`;
      // const reqURL = `/users/wu974cBkmeVi4vfmQw0uSJUOCX83/muisc`;
      vm.isLoading = true;
      vm.$http.post(url, {"url": reqURL}).then((response) => {
        vm.songList = response.data;
        vm.isLoading = false;
      })
      .then((result) => {
        vm.songList.forEach(val => {
          if(val.like) {val.like = Object.keys(val.like); }         
        });
      })
      .then(result => {
        vm.songList.forEach(val => {
          if(val.type) {val.type = Object.values(val.type); }         
        });
      })
      .catch(e => {
        vm.isLoading = false;
      })
    },
  },
  created() {
    const vm = this;
    vm.loginOrNot();
    vm.getMusicData();
    vm.$bus.$on('audio:list',(list) => {
      vm.songList = list;
      setTimeout(() => {
        vm.$refs.aplayer.switch(0);
      }, 0);      
    })
    vm.$bus.$on('audio:newList',(element) => {
      vm.songList = element    
    })
    vm.$bus.$on('audio:pause',() => {
      this.$refs.aplayer.pause();     
    })
    vm.$bus.$on('audio:play',(item) => {
      const song = vm.songList;
      song.forEach((i, index) => {
        if(i.id === item.id) {
          vm.$refs.aplayer.switch(index);
          vm.$refs.aplayer.toggle();
        }
      }); 
    })
    // console.log(vm.$refs.aplayer.currentMusic)
  },
}
</script>

