import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('./views/Home.vue')
    },
    {
      path: '/partner',
      name: 'partner',
      component: () => import('./views/Partner.vue'),
      children: [
        {
          path: '/partner/post/:id',
          name: 'Post',
          component: () => import('./views/pages/PostPartner.vue'),
          children: [
            {
              path: '/partner/post/:id/resume',
              name: 'resume',
              component: () => import('./views/pages/SendResume.vue'),
              meta: { requiresAuth: true },
            },
          ]
        }
      ],
    },
    {
      path: '/show',
      name: 'show',
      component: () => import('./views/Show.vue'),
      children: [
        {
          path: '/show/post/:id',
          name: 'showPost',
          component: () => import('./views/pages/PostShow.vue'),
          children: [
            {
              path: '/show/post/:id/resume',
              name: 'resume',
              component: () => import('./views/pages/SendResume.vue'),
              meta: { requiresAuth: true },
            },
          ]
        }
      ],
    },
    {
      path: '/partner/creat',
      name: 'CreatPartner',
      component: () => import('./views/pages/CreatPartner.vue'),
      meta: { requiresAuth: true },
    },
    {
      path: '/show/creat',
      name: 'CreatShow',
      component: () => import('./views/pages/CreatShow.vue'),
      meta: { requiresAuth: true },
    },
    {
      path: '/signup',
      name: 'signup',
      component: () => import('./views/Signup.vue')
    },
    {
      path: '/music',
      name: 'Music',
      component: () => import('./views/Music.vue'),
      children: [
        {
          path: '/music/all',
          name: 'AllSong',
          component: () => import('./views/pages/MusicAllSong.vue'),
        },
        {
          path: '/music/loveSong',
          name: 'loveSong',
          component: () => import('./views/pages/MusicLoveSong.vue'),
          meta: { requiresAuth: true },
        }
      ]
    },
    {
      path: '/user',
      name: 'User',
      component: () => import('./views/User.vue'),
      meta: { requiresAuth: true },
      children: [
        {
          path: '/user/profile',
          name: 'UserProfile',
          component: () => import('./views/pages/UserProfile.vue'),
          meta: { requiresAuth: true },
        },
        {
          path: '/user/creatMusic',
          name: 'creatMusic',
          component: () => import('./views/pages/CreatMusic.vue'),
          meta: { requiresAuth: true },
        },
        {
          path: '/user/editAccount',
          name: 'UserEdit',
          component: () => import('./views/pages/UserEdit.vue'),
          meta: { requiresAuth: true },
        },
        {
          path: '/user/music-editor/:id',
          name: 'MusicEditor',
          component: () => import('./views/pages/MusicEditor.vue'),
          meta: { requiresAuth: true },
        },
      ]
    },
  ]
})
