<template>
  <div class="myscrollbar">
    <Login v-if="dom.login"/>
    <div id="menu-toggle" class="menu-sm">
      <img src="../assets/img/menu/logo-s.jpg" height="50" alt="迷聲">
      <a href="#" class="btn btn-dark btn-lg toggle" @click="dom.opened = true"><i class="fas fa-bars"></i></a>
    </div>
    <div class="menu-close animated fadeIn faster" v-if="dom.opened" @click="dom.opened = false"></div>
    <!-- <a id="menu-close" href="#" class="btn bt/n-light btn-lg pull-right toggle"><i class="fa fa-times"></i></a> -->
    <nav id="sidebar-wrapper" :class="{'closed': !dom.opened}">
      <div class="topmenu">
        <div class="user-info">
          <div class="con">
            <h1>迷聲 音樂交流平台</h1>
            <div class="input-group searching">
              <button class="btn" type="button" @click="search()">
                <i class="fas fa-search"></i>
              </button>
              <input type="text" class="form-control" placeholder="搜尋"
                v-model="modelData.search" @keydown.enter="search()">          
            </div>
            <div class="echoin" v-if="userData">
              <img src="../assets/img/menu/echoin.jpg" alt="echoin"> 
                Echoin : <span>{{userData.echoin}}</span>
            </div>
            <div class="logout-btn" @click="dom.login = true;dom.opened = false" v-if="!userData">
              <a href="#" @click.prevent="">註冊會員 / 登入會員</a>
            </div>
          </div>
          <router-link to="index.html" class="pic-info" v-if="userData" title="個人空間">
            <div class="stickers">
              <div class="personal-photo" v-if="userData.photoURL" :style="{'backgroundImage': `url(${userData.photoURL})`}"></div>
            </div>
            <p data-toggle="tooltip" data-placement="bottom" :title="userData.displayName" class="userName">{{userData.displayName}}</p>
          </router-link>
        </div>
        <ul class="sidebar-nav" @click="dom.opened = false">
          <li>
            <router-link to="/" title="迷聲音樂交流平台">
              <img src="../assets/img/menu/index-icon.svg" height="40" alt="Meason">首頁　<small>Home</small>
            </router-link>
          </li>
          <li>
            <router-link to="/partner" title="找音樂夥伴">
              <img src="../assets/img/menu/partner-icon.svg" height="40" alt="找夥伴">找夥伴　<small>Find a partner</small>
            </router-link>
          </li>
          <li>
            <router-link to="/user/profile" title="個人創作空間">
              <img src="../assets/img/menu/personal-icon.svg" height="40" alt="個人空間">個人空間　<small>Own page</small>
            </router-link>
          </li>
          <li>
            <router-link to="/show" title="音樂表演機會">
            <img src="../assets/img/menu/active-icon.svg" height="40" alt="表演機會">我想表演　<small>Performance</small>
          </router-link>
          </li>
          <li>
            <router-link to="/music/all" title="音樂曲庫">
              <img src="../assets/img/menu/music-icon.svg" height="40" alt="音樂曲庫">曲庫　<small>Music library</small>
            </router-link>
          </li>
          <li>
            <router-link to="/shop" title="Echion 商城">
              <img src="../assets/img/menu/echion-icon.svg" height="40" alt="Echion 商城">Echion 商城　<small>Echion store</small>
            </router-link>
          </li>
          <li>
            <a href="https://www.facebook.com/measonmusic" target="_blank" title="聯絡粉絲專頁">
              <img src="../assets/img/menu/messenger-icon.svg" height="40" alt="聯絡迷聲粉絲專頁">聯絡粉專　<small>Contact FB</small>
            </a>
          </li>
        </ul>        
      </div>
      <div class="account" v-if="userData">
        <router-link to="/user/editAccount" title="編輯帳號">編輯帳號 <small>edit account</small></router-link><br>
        <a href="#" @click.prevent="logout()" title="登出帳號">登出帳號 <small>logout</small></a>
      </div>
    </nav>
  </div>
</template>

<script>
import firebase from 'firebase';
import Login from './Login.vue'

export default {
  props: ['user'],
  data() {
    return {
      userData: {},
      dom: {
        opened: false,
        login: false,
      },
      modelData: {
        search: '',
      },
      temp: '',
    }
  },
  components:{
    Login
  },
  methods: {
    search() {
      const vm = this;
      const url = `https://www.google.com/search?lr=&hl=zh-TW&q=${vm.modelData.search}+site%3Ahttps%3A%2F%2Fmeasonmusic.com`;
      window.location = url;
    },
    logout() {
      const vm = this;
      firebase.auth().signOut().then(function() {
        vm.userData = null;
      }, function(error) {
        console.log("User sign out error!");
      })
    },
    getUserData() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getData`;
      firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
          vm.$http.post(url, {"url": `users/${user.uid}/basic/`}).then((response) => {
            vm.userData = response.data;
          })
        } else {
          vm.userData = null;
        }
      });
    }
  },
  created() {
    const vm = this;
    vm.$bus.$on('close:login',(val) => {
      vm.dom.login = val;
    })
    vm.getUserData();
  },
}
</script>

<style lang="scss" scoped>
$bg: #202020;
$oo: #ffb949; 
/* Side Menu */
h1{
  height: 0;
  overflow: hidden;
}
a{
  transition: all 0.2s ease-in-out;
  small{
    transition: all 0.2s ease-in-out;
  }
  &:hover{
    text-decoration: none;
    color: $oo;
    small{
      color: $oo;
    }
  }
}
.myscrollbar{
  *{
    color: #fff;
  }
  #sidebar-wrapper{
    width: 320px;
    max-width: 90vw;
    flex: 0 0 320px;
    background-color: $bg;
    padding: 30px 30px 40px 30px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all 0.5s ease-in-out;
    overflow: auto;
    overflow-x: hidden;
    &::-webkit-scrollbar {
      width: 8px;
      height: 8px;
    }
    &::-webkit-scrollbar-track {
      background: #202020;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #202020;
    }
    &::-webkit-scrollbar-thumb:hover {
      background-color: #202020;
    }
    .sidebar-nav {
      padding: 0;
      list-style: none;
    }
    .user-info{
      display: flex;
      justify-content: space-between;
      .echoin{
        margin-top: 20px;
      }
      .logout-btn{
        margin-top: 20px;
      }
      .searching {
        width: 180px;
        input {
          border: none;
          border-radius: 0 20px 20px 0;
          height: 36px;
          margin-left: -1px;
          background-color: #eee;
          padding-left: 0;
          &:focus{
            box-shadow: none;
          }
        }
        .btn {
          border-radius: 20px 0 0 20px;
          border: none;
          background: #eee;
          cursor: pointer;
          svg{
            color: #555;
          }
        }
      }
    }
    .pic-info{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      max-width: 100px;
      .stickers{
        width: 50px;
        height: 50px;
        background-size: cover;
        border-radius: 4px;
        background-image: url('../assets/img/undefine.jpg');
        position: relative;
        overflow: hidden;
        .personal-photo{
          position: absolute;
          background-size: cover;
          background-position: center center;
          width: 100%;
          height: 100%;
        }
      }
      p{
        margin-bottom: 0;
        margin-top: 5px;
        white-space: nowrap;
        max-width: 135px;
        overflow: hidden;
        text-overflow: ellipsis;
        text-align: center;
      }
    }
    .sidebar-nav{
      margin-top: 30px;
      li{
        a{
          display: flex;
          align-items: center;
          height: 60px;
          border-top: 1px #686868 solid;
          img{
            transition: all 0.2s ease-in-out;
            margin-right: 15px;
          }
          &:hover{
            img{
              transform: scale(0.9, 0.9);
            }
          }
          &.router-link-exact-active{
            // text-shadow: 1px 1px 10px rgba(#fff7c8, 0.9);
            color: #fecd88;
            small{
              color: #fecd88;
            }
          }
        }
        &:first-child a{
          border: none;
        }
      }
    }
    .account{
      margin-top: 30px;
    }
  }
  #menu-toggle{
    display: none;
  }
  .menu-close{
    background-color: rgba(#000, 0.2);
  }
  @media (max-width: 1200px){
    #menu-toggle{
      position: fixed;
      top: 0;
      width: 100%;
      left: 0;
      display: flex;
      background: #323232;
      justify-content: space-between;
      z-index: 10;
    }
    #sidebar-wrapper{
      position: fixed;
      z-index: 1000;
      right: 0px;
      top: 0;
      &.closed{
        right: -330px;
      }
    }
    .menu-close{
      display: block;
      position: fixed;
      z-index: 100;
      width: 100%;
      height: 100%;
      background-color: rgba(#000, 0.1);
      top: 0;
      left: 0;
    }
  }
}
</style>
