<template>
  <div class="login-content animated fadeIn faster">
    <loading :active.sync="isLoading">
      <img alt="Meason" src="../assets/img/loading.svg" width="100">
    </loading>
    <div class="close-area" @click="closeModal()"></div>
    <div class="con">
      <div class="tab"><i class="fa fa-compact-disc"></i></div>
      <h3>會員登入</h3>
      <input class="form-control" type="email" placeholder="請在此輸入電子信箱"
        v-model="mail" @click="errorCode = ''" @keydown.enter="login()">
      <input class="form-control" type="password" placeholder="請在此輸入密碼"
        v-model="pwd" @click="errorCode = ''" @keydown.enter="login()">
      <div v-if="errorCode" class="text-danger">{{errorCode}}</div>
      <article>
        <button class="btn btn-light" @click="resetPwd()">忘記密碼</button>
        <button class="btn btn-primary" @click="login()">立即登入</button>
      </article>
      <div class="quick-sign-hr"><p>或是</p></div>
      <div class="signup">
        <button class="btn btn-fb btn-outline-dark" role="button" type="button"
          @click="fbLogin()">
        以Facebook帳號繼續</button>
        <a href="#" class="btn btn-outline-dark" 
          @click.prevent="linkToSignUp()">免費註冊</a>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase';

export default {
  name: 'Login',
  data() {
    return {
      isLoading: false,
      mail: '',
      pwd: '',
      errorCode: '',
    }
  },
  methods: {
    fbLogin() {
      const vm = this;
      vm.isLoading = true;
      var provider = new firebase.auth.FacebookAuthProvider();
      firebase.auth().signInWithPopup(provider).then(function(result) {
        // 取得FB Token，可以使用於FB API中
        var token = result.credential.accessToken;
        // 使用者資料
        var FBUser = result.user;        
        if (result.additionalUserInfo.isNewUser){
          // 如果是新使用者
          const req_data = {
            "uid": FBUser.uid,
            "user": {
              email: FBUser.email,
              displayName: FBUser.displayName,
              photoURL : FBUser.photoURL,
              echoin: 0,
              uid: FBUser.uid,
            }
          }
          vm.loginApi(req_data);
        } else {
          console.log('登入過ㄌ')
        }
        vm.closeModal();
      }).catch(function(error) {
        //處理 帳號已經存在於其他登入方式時
        if (error.code === 'auth/account-exists-with-different-credential') {          
          var pendingCred = error.credential; // 取得credential
          var email = error.email;          
          firebase.auth().fetchSignInMethodsForEmail(email).then(function(providers) {
            // 取得當初此Email的登入方式
            if (providers[0] === 'password') {
              // 如果使用者有多個登入方式的話
              alert('此信箱已註冊過，請改用一般登入方式。')
              return;
            }
          });
        }
      });
    },
    loginApi(req) {
      const url = `${process.env.VUE_APP_API}/fbLogin`;
      this.$http.post(url, {"uid": req.uid, "user": req.user}).then((response) => {
        if (response.data.success) {
          console.log('6666')
        }
        console.log('FB 登入成功',response)
      })
      .catch(function(error){
        console.error("寫入使用者資訊錯誤",error);
      });
    },
    promptForgetPwd(){
      var pwd = prompt("請輸入您的帳號(email)");
      if (pwd != null) {
        return pwd;
      }
    },
    resetPwd() {
      var password = this.promptForgetPwd();
      firebase.auth().sendPasswordResetEmail(password).then(function() {
        // Email sent.
        console.log("更改密碼Email已發送");
      }, function(error) {
        // An error happened.
        console.error("更改密碼",error);
        alert(error.message)
      });
    },
    login() {
      const vm = this;
      vm.isLoading = true;
      // const url = `${process.env.VUE_APP_API}/login`;
      // this.$http.post(url, {"mail": vm.mail, "pwd": vm.pwd}).then((response) => {
      //   if (response.data.success) {
      //     console.log(response)
      //     vm.closeModal();
      //   } else {
      //     console.log("寫入使用者資訊錯誤", response)
      //     const error = response.data.result;
      //     const errorMessage = error.message;
      //     if(errorMessage === 'The email address is badly formatted.'){
      //       vm.errorCode = '電子郵件地址格式錯誤'
      //     } else if(errorMessage === 'The password is invalid or the user does not have a password.') {
      //       vm.errorCode = '密碼錯誤或尚未填入密碼'
      //     } else if(errorMessage === "There is no user record corresponding to this identifier. The user may have been deleted.") {
      //       vm.errorCode = '此電子信箱尚未註冊'
      //     } else {
      //       vm.errorCode = '登入失敗' + errorMessage
      //     }
      //     vm.isLoading = false;
      //   }
      // })
      // .catch(function(error){
      //   alert('登入失敗');
      //   console.log("錯誤",error);
      // });
      firebase.auth().signInWithEmailAndPassword(vm.mail, vm.pwd).then(function(result) {
        // 使用者資料
        var user = result.user;
        console.log(user)
        vm.closeModal();
      }).catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        if(errorMessage === 'The email address is badly formatted.'){
          vm.errorCode = '電子郵件地址格式錯誤'
        } else if(errorMessage === 'The password is invalid or the user does not have a password.') {
          vm.errorCode = '密碼錯誤或尚未填入密碼'
        } else if(errorMessage === "There is no user record corresponding to this identifier. The user may have been deleted.") {
          vm.errorCode = '此電子信箱尚未註冊'
        } else {
          vm.errorCode = '登入失敗' + errorMessage
        }
        vm.isLoading = false;
      }).then(() => {
        if(!vm.errorCode){
          vm.closeModal();
        } 
      })
    },
    closeModal() {
      const vm = this;
      vm.isLoading = false;
      this.$bus.$emit('close:login', false);
    },
    linkToSignUp() {
      const vm = this;
      vm.$router.push('/signup');
      this.$bus.$emit('close:login', false);
    }
  },
}
</script>

<style lang="scss" scoped>
$light-yellow: #fce389;
$main-org: #fd8622;

.close-area{
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(#222, .5);
}
.login-content{
	display: flex;
	justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 2000;
  top: 0;
  left: 0;
  width: 100%;
  // background-image: url('../assets/img/bg/login.jpg');
	background-position: center;
	background-size: cover;
	height: 100vh;
	.con{
		box-shadow: -1px -1px 30px rgba(0,0,0,0.5);
		background-color: rgba(255,255,255,1);
		display: flex;
		flex-direction: column;
		padding: 40px 30px 60px 30px;
		border-radius: 10px;
		min-width: 400px;
		max-width: 95%;
		position: relative;
		.tab{
      text-align: center;
			font-size: 35px;
			color: $light-yellow;
			padding-top: 4px;
			position: absolute;
			top: -7px;
			content: '';
			width: 50px;
			height: 61px;
			background-color: $main-org;
			border-radius: 3px;
			right: 35px;
			svg{transition: all 5s ease-in-out;}
			&:hover svg{
        transform: rotate(10800deg);
      }
    }
		h3{
			border-bottom: 1px #999 solid;
			padding-bottom: 15px;
      margin-bottom: 10px;
      color: #333;
    }
		input{
      margin-top: 15px;
    }
		article{
      align-self: flex-end;
      margin-top: 15px;
    }
		.quick-sign-hr{
			border-top: 1px #ccc solid;
			margin-top: 30px;
			display:flex;
      align-items: center;
      justify-content: center;
			p{
				text-align: center;
				position: relative;
				top: -15px;
				margin-bottom: 0;
				width: max-content;
				background: #fff;
        padding: 0 7px;
        color: #333;
      }
    }
		.signup{
			.btn{
				background-color: transparent;
				border-radius: 25px;
				border: 1px #555 solid;
				margin-bottom: 10px;
				&:hover{
					color: #999;
          border-color: #999;
        }
      }
			.btn-fb{
				background-color: rgb(66, 103, 178);
				color: #fff;
        border: none;
        &:hover{
          color: #fff;
          background-color: $main-org;
        }
      }
    }
	.btn{
		transition: all 0.2s ease-in-out;
    cursor: pointer;
    margin-left: 5px;
    &.btn-primary{
      color: #fff;
      font-weight: bold;
    }
		&.btn-light{
      background-color: #e6e6e6;
			&:hover{background-color: #ddd}
    }
  }
}
}
@media (max-width: 576px){
	.login-content{
    .con{min-width: auto}
  }
}

</style>
